import Immutable from 'immutable';
import { actionTypes } from '~components/srp-map/js/actions';

export const MAP_DEFAULT_STATE = Immutable.Map({
  activeProperty: null,
  selectedUniversity: null,
  properties: null,
});

export function srpMapReducer(state = MAP_DEFAULT_STATE, action) {
  switch (action.type) {
    case actionTypes.INITIAL_DATA: {
      return state.mergeDeep(action.payload);
    }
    case actionTypes.CHANGE_UNIVERSITY: {
      return state.merge({
        selectedUniversity: action.payload,
        center: null,
      });
    }
    case actionTypes.CHANGE_PROPERTY: {
      return state.merge({
        activeProperty: action.payload,
        center: action.payload,
      });
    }
    case actionTypes.RESET_PROPERTIES: {
      const newState = {
        isVisible: true,
        properties: action.payload.properties,
        center: null,
      };

      if (action.payload.properties.length > 0) {
        newState.activeProperty = action.payload.properties[0].slug;
      }

      if (action.payload.updateCenter) {
        newState.center = action.payload.properties[0];
      } else if (state.toJS().selectedUniversity) {
        newState.center = state.toJS().selectedUniversity;
      }

      return state.merge(newState);
    }
    case actionTypes.OPEN_MAP: {
      return state.merge({
        isVisible: true,
        center: null,
        ...action.payload,
      });
    }
    case actionTypes.CLOSE_MAP: {
      return state.merge({
        isVisible: false,
        center: null,
      });
    }
    default: {
      return state;
    }
  }
}



