/* global mapboxgl, MapboxGeocoder */
import axios from '~global-js/helpers/axios';
import polyline from 'polyline';
import handlebars from 'handlebars/runtime';
import window from 'global/window';
import { all as promiseAll } from 'rsvp';
import BaseComponent from '~global-js/component/base-component';
import { formatCurrency } from '~global-js/helpers/currency';
import * as actions from '~components/srp-map/js/actions';
import accessToken from '~components/srp-map/js/setting';
import importScript from '~global-js/helpers/import-script';
import importStyle from '~global-js/helpers/import-style';
import { mapboxGLPaths } from '~global-js/settings/paths';
import * as breakpoints from '~global-js/helpers/breakpoints';
import propertyPinTemplate from '~components/srp-map/templates/property-pin.hbs';


const ROUTE_TYPES = ['bicycling', 'driving', 'walking'];
const ROUTE_COLORS = [
  {
    color: '#38B2A6',
    opacity: 0.5,
  },
  {
    color: '#6D7FB4',
    opacity: 0.7,
  },
  {
    color: '#FD8649',
    opacity: 0.5,
  },
];
const ROUTES_WIDTH = 4;

export default class SrpMap extends BaseComponent {
  /**
   * construct the component
   *
   * @param element
   * @param props
   */
  constructor(element, props = {}) {
    if (!props.getState || !props.dispatch || !props.subscribe) {
      throw new Error('You must specify props.getState, props.dispatch and props.subscribe');
    }
    super(element, props);
    this.state = {
      map: null,
      activeProperty: null,
      previousState: null,
      currentState: null,
      selectedUniversity: null,
      properties: null,
      markers: [],
      universityMarker: null,
      prevUniversity: null,
      prevProperty: null,
      prevZoom: null,
      offset: null,
    };
  }

  /**
   * component init function
   *
   * - if browser support mapbox GL
   *   - load mapbox scripts and styles
   *   - load translation namespace
   *   - load necessary properties and universities data
   *
   * - if browser not support render a static map
   */
  init() {

    if (SrpMap.supportMapboxGL()) {
      promiseAll([
        importStyle(mapboxGLPaths.mapboxGLCss),
        importStyle(mapboxGLPaths.geocoderCss),
        importScript(mapboxGLPaths.mapboxGLScript),
        importScript(mapboxGLPaths.geocoderScript),
      ]).then(() => {
        this.props.i18n.loadNamespaces(['map', 'property_page', 'property_detail_rooms'], () => {
          this.props.subscribe(this.reRender);
          this.props.dispatch(
            actions.addInitData({
              allProperties: this.props.allProperties,
              activeProperty: this.props.activeProperty,
              selectedUniversity: this.props.selectedUniversity,
            })
          );
        });
      });
    } else {

    }
  }

  /**
   * check if browser supports mapbox GL
   *
   * @returns {boolean}
   */
  static supportMapboxGL() {
    if (typeof window.navigator !== 'undefined') {
      const msie = window.navigator.userAgent.indexOf('MSIE');
      const trident = window.navigator.userAgent.indexOf('Trident/');

      const canvas = document.createElement('canvas');
      const gl = canvas.getContext('webgl') || canvas.getContext('experimental-webgl');

      return msie === -1 && trident === -1 && gl && gl instanceof WebGLRenderingContext;
    }

    return false;
  }

  /**
   * add basci map to container
   *
   * @returns {mapboxgl.Map}
   */
  addMap() {
    mapboxgl.accessToken = accessToken;
    const map = new mapboxgl.Map({
      container: this.element,
      style: this.props.mapConf.style,
    });

    map.dragRotate.disable();
    map.touchZoomRotate.disableRotation();
    this.addResizeListener();

    return map;
  }


  /**
   * reRender the map once the state changed
   *
   * - if map is visible
   *    - if map hasn't been initialized before,
   *      initialize the map and add universities and properties
   *    - if map has been initialized before, just update the universities and properties
   * - if map is not visible
   *    - hide the map
   */
  reRender = () => {
    if (document.querySelector('.map__loading--active')) {
      document.querySelector('.map__loading--active').classList.remove('map__loading--active');
    }

    if (!this.state.currentState) {
      // first Loading without data
      this.state.map = this.addMap();
      this.state.map.on('zoomend', () => {
        const zoom = this.state.map.getZoom();
        if (zoom !== this.state.prevZoom) {
          this.state.prevZoom = zoom;
          this.props.zoomChanged(zoom);
        }
      });

      this.updateState();
      this.fitToBounds();
      if (this.state.selectedUniversity) {
        this.state.map.jumpTo({
          center: [this.state.selectedUniversity.longitude, this.state.selectedUniversity.latitude],
        });
      } else if (this.state.activeProperty) {
        this.state.map.jumpTo({
          center: [this.state.activeProperty.longitude, this.state.activeProperty.latitude],
        });
      }
    } else if (!this.state.previousState) {
      // init with data
      this.updateState();
      this.addProperties();

      if (this.state.selectedUniversity) {
        this.addUniversity();
      }

      if (this.state.selectedUniversity && this.state.activeProperty) {
        this.addRoutes();
      }
    } else {
      this.updateState();

      if (
          (this.state.properties && JSON.stringify(this.state.activeProperty) !== JSON.stringify(this.state.previousState.get('activeProperty'))) ||
          (this.state.properties && JSON.stringify(this.state.properties) !== JSON.stringify(this.state.previousState.get('properties').toJS()))
      ) {
        this.addProperties();
      }

      if (
          JSON.stringify(this.state.selectedUniversity) !== JSON.stringify(this.state.previousState.get('selectedUniversity'))
      ) {
        this.addUniversity();
      }

      const property = this.state.activeProperty;
      if (
          this.state.selectedUniversity &&
          (this.state.lastChange === 'university' || this.state.lastChange === 'property')
      ) {
        this.addRoutes();

        if (this.state.lastChange === 'university') {
          this.state.map.setZoom(12);
        }

        if (this.state.currentState.get('center')) {
          let center = this.state.currentState.get('center');
          center = typeof center === 'string' ? property : center.toJS();
          this.state.map.jumpTo({
            center: [center.longitude, center.latitude],
          });
        }
      } else if (!this.state.selectedUniversity) {
        this.removeRoutes();

        if (this.state.lastChange === 'property' && property) {
          this.state.map.jumpTo({
            center: [property.longitude, property.latitude],
            offset: [0, -200],
          });
        }
      }
    }
  };

  updateState() {
    if (this.state.map) {
      this.state.map.resize();
    }

    // set new state and get the
    this.state.previousState = this.state.currentState;
    this.state.currentState = this.props.getState();
    this.state.selectedUniversity = this.state.currentState.get('selectedUniversity') ? this.state.currentState.get('selectedUniversity').toJS() : null;
    this.state.properties = this.state.currentState && this.state.currentState.get('properties') ? this.state.currentState.get('properties').toJS() : null;

    let lastChange = null;

    if (
        !this.state.previousState ||
        (this.state.previousState && !this.state.previousState.get('selectedUniversity') && this.state.selectedUniversity) ||
        (this.state.previousState &&
        this.state.previousState.get('selectedUniversity') &&
        JSON.stringify(this.state.previousState.get('selectedUniversity').toJS()) !== JSON.stringify(this.state.selectedUniversity))
    ) {
      lastChange = 'university';
    } else if (
        this.state.activeProperty &&
        this.state.previousState.get('activeProperty') &&
        (this.state.activeProperty.slug !== this.state.previousState.get('activeProperty') || this.state.currentState.get('center'))
    ) {
      lastChange = 'property';
    }

    this.state.lastChange = lastChange;
  }

  addUniversity() {
    if (this.state.universityMarker) {
      this.state.universityMarker.remove();
    }

    if (this.state.selectedUniversity && this.state.selectedUniversity !== 'null') {
      const lngLat = [
        this.state.selectedUniversity.longitude,
        this.state.selectedUniversity.latitude,
      ];
      const slug = this.state.selectedUniversity.slug;
      const el = document.createElement('div');
      el.className = `marker marker--university university--${slug}`;
      el.setAttribute('data-university', JSON.stringify(this.state.selectedUniversity));
      el.innerHTML = '<div class="university"></div>';

      const offset = this.getOffset();
      const marker = new mapboxgl.Marker(el, {
        offset,
      }).setLngLat(lngLat);

      marker.addTo(this.state.map);
      this.state.universityMarker = marker;
    }
  }

  addResizeListener() {
    window.addEventListener('resize', () => {
      if (this.state.map) {
        this.state.map.resize();
      }
    });
  }

  /**
   * generate the url to request routes
   *
   * @param {string} type
   * @param {number} longitude1
   * @param {number} latitude1
   * @param {number} longitude2
   * @param {number} latitude2
   *
   */
  static generateRoutesUrl(type, longitude1, latitude1, longitude2, latitude2) {
    let communRoutes = mapboxGLPaths.communRoutes;
    communRoutes = communRoutes.replace('{type}', type)
        .replace('{longitude1}', longitude1)
        .replace('{latitude1}', latitude1)
        .replace('{longitude2}', longitude2)
        .replace('{latitude2}', latitude2);
    return communRoutes + accessToken;
  }

  /**
   * remove all routes
   */
  removeRoutes() {
    [].slice.call(document.querySelectorAll('.marker--route')).forEach((routeDom) => {
      routeDom.remove();
    });

    for (let i = 0; i < 3; i++) {
      if (this.state.map.getLayer(`route${i}`)) {
        this.state.map.removeLayer(`route${i}`);
      }

      if (this.state.map.getSource(`route${i}`)) {
        this.state.map.removeSource(`route${i}`);
      }
    }

    const commuteTime = document.querySelector('.map__commute-time');

    if (commuteTime) {
      commuteTime.classList.add('hide');
    }
  }

  /**
   * add routes to the map
   * @param {object} property
   * @param {object} university
   */
  addRoutes() {
    const property = this.state.activeProperty;
    const university = this.state.selectedUniversity;

    if (property) {
      delete property.reviews;
    }

    // will update two times for university change
    if (
        (
          JSON.stringify(property) !== JSON.stringify(this.state.prevProperty) ||
          this.state.currentState.get('center')
        ) &&
        property && university
    ) {
      const commuteTimes = property.commute_time;
      const commuteTimeNode = document.querySelector('.map__commute-time');
      let content = '';
      this.state.prevUniversity = university;
      this.state.prevProperty = property;
      axios.all([
        axios.get(SrpMap.generateRoutesUrl('walking', property.longitude, property.latitude, university.longitude, university.latitude)),
        axios.get(SrpMap.generateRoutesUrl('cycling', property.longitude, property.latitude, university.longitude, university.latitude)),
        axios.get(SrpMap.generateRoutesUrl('driving', property.longitude, property.latitude, university.longitude, university.latitude)),
      ])
      .then((responses) => {
        this.removeRoutes();
        if (this.state.selectedUniversity && this.state.activeProperty) {
          let routeCount = 0;
          responses.forEach((response) => {
            let geometry = response.data.routes[0].geometry;
            geometry = typeof geometry === 'string' ? polyline.toGeoJSON(geometry) : geometry;
            const feature = {
              type: 'Feature',
              properties: {},
              geometry,
            };

            if (typeof this.state.map.getSource(`route${routeCount}`) === 'undefined') {
              this.state.map.addSource(`route${routeCount}`, {
                type: 'geojson',
                data: feature,
              });
            }

            if (typeof this.state.map.getLayer(`route${routeCount}`) === 'undefined') {
              this.state.map.addLayer({
                id: `route${routeCount}`,
                type: 'line',
                source: `route${routeCount}`,
                layout: {
                  'line-join': 'round',
                  'line-cap': 'round',
                },
                paint: {
                  'line-color': ROUTE_COLORS[routeCount].color,
                  'line-opacity': ROUTE_COLORS[routeCount].opacity,
                  'line-width': ROUTES_WIDTH,
                  'line-translate': this.getOffset(),
                },
              });
            }

            routeCount += 1;
          });

          if (commuteTimes) {
            ROUTE_TYPES.forEach((type) => {
              const markerDuration = Math.round(commuteTimes[type] / 60);
              const commuteTime = markerDuration >= 2 ? this.props.i18n.t('map:map.popup.commute_time.minutes') : this.props.i18n.t('map:map.popup.commute_time.minute');

              content += `<span class="marker--route-content marker--route-${type}">${markerDuration} ${commuteTime}</span>`;
            });
            commuteTimeNode.classList.remove('hide');
            commuteTimeNode.querySelector('.map__commute-time-content').innerHTML = content;
          }
        }
      });
    } else {
      this.removeRoutes();
    }
  }

  /**
   * add properties to the map
   */
  addProperties() {
    if (this.state.markers.length > 0) {
      this.state.markers.forEach((marker) => {
        marker.remove();
      });
    }

    if (this.state.properties && this.state.properties.length > 0) {
      this.state.properties.forEach((property, index) => {
        this.addProperty(property, index);
      });
    } else {
      this.state.activeProperty = null;
      this.removeRoutes();
    }
  }

  /**
   * add property to the map and bind click listener
   *
   * @param property
   */
  addProperty(property, index) {
    const el = document.createElement('div');
    const price = property.state === 'available_with_price' ? formatCurrency(this.props.i18n, property.from_price, property.currency) : null;
    const discountedPrice = property.discounted_from_price ?
        formatCurrency(this.props.i18n, property.discounted_from_price, property.currency) : null;

    const content = propertyPinTemplate({
      state: property.state ? property.state.replace(/_/g, '-') : 'sold-out',
      price,
      discountedPrice,
      property,
      isPP: this.props.propertyData,
    });

    const offset = this.getOffset();
    if (property.slug === this.state.currentState.get('activeProperty')) {
      this.state.activeProperty = property;
      el.className = 'marker marker--property marker--active';
      offset[0] = -35;
    } else {
      el.className = 'marker marker--property';
      offset[0] = -29;
    }

    el.innerHTML = content;

    el.addEventListener('click', () => {
      if (this.props.propertyClick) {
        this.props.propertyClick(property.slug, index);
      }
    });

    // add marker to map
    const marker = new mapboxgl.Marker(el, {
      offset,
    }).setLngLat([property.longitude, property.latitude])
        .addTo(this.state.map);

    this.state.markers.push(marker);
  }

  /**
   * get all cordinates from properties and universities
   *
   * @param properties
   * @param universities
   * @returns {Array}
   */
  static getAllCordinates(properties) {
    const cordinates = [];

    properties.forEach((property) => {
      cordinates.push([property.longitude, property.latitude]);
    });
    return cordinates;
  }

  getOffset() {
    const topHeight = document.querySelector('.map__header').offsetHeight;
    let offset = [0, -(topHeight / 2)];
    if (this.state.offset) {
      offset = this.state.offset;
    } else if (this.props.allProperties) {
      const bottomHeight = 160; // Property cards not ready here, so use const

      // ajust the map according to design
      if (breakpoints.isMediumScreen()) {
        offset = [0, (topHeight - bottomHeight) / 2];
      } else {
        // property cards height - universities height
        offset = [0, (topHeight - bottomHeight) / 2];
      }
    }

    return offset;
  }

  /**
   * fit view to a bounds
   *
   * @param initCoordinates
   */
  fitToBounds(initCoordinates = null) {
    let coordinates = initCoordinates;
    if (!coordinates) {
      coordinates = SrpMap.getAllCordinates(this.state.currentState.get('allProperties').toJS());
    }

    const padding = 32;

    const bounds = coordinates.reduce(
        (newBounds, coord) => (
            newBounds.extend(coord)
        ),
        new mapboxgl.LngLatBounds(coordinates[0], coordinates[0])
    );

    this.state.map.fitBounds(bounds, {
      padding,
      offset: this.getOffset(),
      animate: false,
    });

    const zoom = this.state.map.getZoom();
    if (zoom !== this.state.prevZoom) {
      this.state.prevZoom = zoom;
      this.props.zoomChanged(zoom);
    }
  }

  /**
   * bind translation helper before render template
   */
  onBeforeTemplateRender() {
    handlebars.registerHelper('trans', key => this.props.i18n.t(key));
  }

  /**
   * rewrite render template function to do additional work
   *
   * @param template
   * @param props
   * @returns {*}
   */
  renderTemplate(template, props) {
    this.onBeforeTemplateRender();
    const result = template(props);
    onAfterTemplateRender();
    return result;
  }

  // eslint-disable-next-line class-methods-use-this
  destroy() {
  }
}


/**
 * unbind translation helper after render the template
 */
function onAfterTemplateRender() {
  handlebars.unregisterHelper('trans');
}



