import Enum from '~global-js/helpers/enum';

export const actionTypes = new Enum(
  'INITIAL_DATA',
  'CHANGE_UNIVERSITY',
  'CHANGE_PROPERTY',
  'OPEN_MAP',
  'CLOSE_MAP',
  'RESET_FILTERS',
  'RESET_PROPERTIES',
  'UPDATE_ROUTES',
);

export function addInitData(payload) {
  return {
    type: actionTypes.INITIAL_DATA,
    payload: {
      ...payload,
    },
  };
}

export function changeProperty(property) {
  return {
    type: actionTypes.CHANGE_PROPERTY,
    payload: property,
  };
}

export function changeUniversity(university) {
  return {
    type: actionTypes.CHANGE_UNIVERSITY,
    payload: university,
  };
}

export function resetProperties(properties, updateCenter = true) {
  return {
    type: actionTypes.RESET_PROPERTIES,
    payload: {
      properties,
      updateCenter,
    },
  };
}

export function openMap(propertyData = null, filters = null) {
  return {
    type: actionTypes.OPEN_MAP,
    payload: {
      selectedProperty: propertyData,
      filters,
    },
  };
}

export function closeMap() {
  return {
    type: actionTypes.CLOSE_MAP,
  };
}



